.container {
  margin: 0 auto;
  width: 70%;
}

@media only screen and (max-width: 600px){
  .home {
    height: 500px;
  }
}

.home-container {
  margin-top: 200px;
  text-align: center;
  height: 400px;
}

.marginTopLink {
  margin-top: 150px;
}

.about-container {
  margin: 0 auto;
  margin-top: 100px;
  width: 70%;
  text-align: center;
}

ul li {
  color: #000;
  text-align: left;
}

.padding {
  padding: 30px;
}

.button-container-contact {
  margin: 0 auto;
  width: 40%;
}

.header {
  margin-top: 100px;
  text-align: center;
}

.contact-container {
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.codeText {
  padding: 20px;
}

.header-description {
  text-align: center;
  margin-top: 40px;
}

.center-button {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.line {
  margin-top: 100px;
  outline: 1px solid #3f51b5;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
}